import React, { useState } from "react";
import classes from "./Inventory.module.css";
import { useSelector } from "react-redux";
import { selectInventory } from "Selectors/Selectors";
import ScrollArea from "react-scrollbar";
import Item from "./Item";
import tables from "GameLogic";
import { DragDropContext } from "react-beautiful-dnd";

class InnerList extends React.Component {
  shouldComponentUpdate(nextProps) {
    if (nextProps.inventory === this.props.inventory) {
      return false;
    }
    return true;
  }

  render() {
    return this.props.inventory.map((item, index) => (
      <Item {...item} index={item.index} />
    ));
  }
}

export default () => {
  const [scroll, setScroll] = useState(false);
  const inventory = [...useSelector(selectInventory)];
  const fullWidth = {
    width: "100%",
    height: "100%",
  };

  const configuredInventory = new Array(tables.Inventory.maxCount)
    .fill(0)
    .map((_, index) => ({ index }));
  inventory.forEach((item) => {
    configuredInventory[item.index] = item;
  });

  const onDragEnd = (result) => {
    console.log(result);
    setScroll(true);
  };

  const onDragStart = (event) => {
    // console.log(event);
    setScroll(false);
  };
  return (
    <ScrollArea
      style={fullWidth}
      speed={1}
      contentClassName={classes.Content}
      horizontal={false}
      vertical={scroll}
    >
      <DragDropContext onDragEnd={onDragEnd} onDragStart={onDragStart}>
        <div className={classes.Items}>
          <InnerList inventory={configuredInventory} />
        </div>
      </DragDropContext>
    </ScrollArea>
  );
};
